package games.studiohummingbird.voyd

import games.studiohummingbird.schema.Place
import games.studiohummingbird.schema.Text
import games.studiohummingbird.schema.Thing
import games.studiohummingbird.voyd.components.Contracts
import games.studiohummingbird.voyd.components.CredentialSelector
import games.studiohummingbird.voyd.components.LocationDetails
import games.studiohummingbird.voyd.pages.CargoPage
import games.studiohummingbird.voyd.pages.PilotOverview
import games.studiohummingbird.voyd.pages.ShipSummaryPage
import games.studiohummingbird.voyd.pages.Sitemap
import games.studiohummingbird.voyd.resources.Site
import js.objects.unsafeJso
import react.FC
import react.Props
import react.router.RouteObject
import react.router.dom.RouterProvider
import react.router.dom.createBrowserRouter
import remix.run.router.ActionFunction
import remix.run.router.ActionFunctionArgs
import remix.run.router.DataFunctionValue
import remix.run.router.LoaderFunction
import web.http.Response
import web.http.Response.Companion.redirect
import web.http.formData
import web.storage.sessionStorage

suspend fun <Context> authenticate(args: ActionFunctionArgs<Context>, handlerCtx: Any?): DataFunctionValue {
    val formData = args.request.formData()
    console.log("action", formData)
    val pilotName = formData.get("pilotName")
    val password = formData.get("password")

    // probably some form of game initialization here
    // hashing, seeding, ec

    console.log(pilotName, password)
    sessionStorage.pilotName = pilotName as String
    sessionStorage.setItem("password", password as String)

    return redirect(
        "${Site.Pilot.INDEX}/$pilotName"
    )
}

val Router = FC<Props> { props ->
    val initialRoutes = arrayOf(
        authenticateRoute(),
        contractsRoute(),
        pilotOverviewRoute(),
        sitemapRoute(),
        locationsRoute(),
        shipsRoute(),
        rootSplatRoute()
    )

    val browserRouter = createBrowserRouter(initialRoutes, unsafeJso {
        basename = "/voyd"
    })

    RouterProvider {
        router = browserRouter
    }
}

external interface ResourceLink {
    var href: String
}

external interface LinkToSelf {
    var self: ResourceLink
}

external interface LinkToNext {
    var next: ResourceLink?
}

external interface LinkToPrevious {
    var prev: ResourceLink?
}

external interface LinkToCargo {
    var cargo: ResourceLink?
}

external interface ResourceLinks : LinkToSelf

external interface ShipResourceLinks : ResourceLinks, LinkToCargo

external interface ResourceObject<L : ResourceLinks> {
    var links: L
}

external interface ThingResource<L : ResourceLinks> : Thing, ResourceObject<L> {
}

external interface ContractsSummaryResource : ThingResource<ResourceLinks>

external interface ShipSummaryResource : ThingResource<ShipResourceLinks>

external interface PlaceResourceObject : ResourceObject<ResourceLinks>, Place

external interface PilotOverviewEmbedded {
    var gameLocation: PlaceResourceObject?
    var ship: ShipSummaryResource?
    var contracts: ContractsSummaryResource?
    var calendar: ThingResource<ResourceLinks>?
}

external interface JsonSchemaInstance {
    var `$schema`: String?
}

external interface PilotOverviewResource : ThingResource<ResourceLinks>, JsonSchemaInstance {
    var embedded: PilotOverviewEmbedded?
}

fun authenticateRoute() =
    RouteObject(
        path = Site.Pilot.AUTHENTICATE,
        action = ActionFunction<Response>(::authenticate),
        Component = CredentialSelector,
    )

fun contractsRoute() =
    RouteObject(
        path = Site.Contracts.INDEX,
        children = arrayOf(
            RouteObject(
                path = ":${Site.Contracts.ID_PARAMETER}",
                Component = Contracts
            )
        )
    )

fun rootSplatRoute() = RouteObject(
    path = "/*",
    loader = LoaderFunction<Response> { _, _ ->
        redirect(Site.Pilot.AUTHENTICATE)
    }
)

fun pilotOverviewResource(pilotName: String): PilotOverviewResource =
    unsafeJso {
        `$schema` = "https://somewhere else/pointing to this structure/with expected links etc"
        links = unsafeJso {
            self = unsafeJso {
                href = "${Site.Pilot.INDEX}/$pilotName"
            }
        }
        embedded = unsafeJso {
            gameLocation = unsafeJso {
                links = unsafeJso {
                    self = unsafeJso {
                        href = "${Site.Locations.INDEX}/cyrus-i"
                    }
                }
                name = Text("Cyrus I")
            }
            ship = unsafeJso {
                links = unsafeJso {
                    self = unsafeJso {
                        href = "${Site.Ships.INDEX}/shuttlecraft"
                    }
                    cargo = unsafeJso {
                        href = "${Site.Ships.INDEX}/shuttlecraft/${Site.Ships.CARGO}"
                    }
                }
                name = Text("Shuttlecraft")
            }
            contracts = unsafeJso {
                links = unsafeJso {
                    self = unsafeJso {
                        href = "${Site.Contracts.INDEX}/cyrus-i-kidnapping-0123456789abcdef"
                    }
                }
                name = Text("Kidnapping at Cyrus I")
            }
        }
        name = Text(pilotName)
    }

fun pilotOverviewRoute() = RouteObject(
    path = Site.Pilot.INDEX,
    children = arrayOf(
        RouteObject(
            path = ":${Site.Pilot.NAME_PARAMETER}",
            Component = PilotOverview,
            loader = LoaderFunction<Response> { args, _ ->
                val pilotName = args.params[Site.Pilot.NAME_PARAMETER]
                // this is the representation of the pilot overview page
                // this is what could be served as a json body from a GET

                pilotName?.let(::pilotOverviewResource)
            }
        )
    ),
)

fun sitemapRoute() = RouteObject(
    path = Site.SITE_MAP,
    Component = Sitemap,
)

/**
 * This includes resource representations from the server.
 */
fun locationsRoute() =
    RouteObject(
        path = Site.Locations.INDEX,
        children = arrayOf(
            RouteObject(
                path = ":${Site.Locations.LOCATION_NAME_PARAM}",
                Component = LocationDetails,
                loader = LoaderFunction<Response> { args, _ ->
                    val locationName = args.params[Site.Locations.LOCATION_NAME_PARAM]

                    locationName?.let {
                        unsafeJso<ThingResource<ResourceLinks>> {
                            name = Text(locationName)
                            links = unsafeJso {
                                self = unsafeJso {
                                    href = "${Site.Locations.INDEX}/${locationName}"
                                }
                            }
                        }
                    }
                },
            )
        )
    )

fun shipsRoute() =
    RouteObject(
        path = Site.Ships.INDEX,
        children = arrayOf(
            RouteObject(
                path = ":${Site.Ships.NAME_PARAMETER}",
                Component = ShipSummaryPage,
                loader = LoaderFunction<Response> { args, _ ->
                    val shipName = args.params[Site.Ships.NAME_PARAMETER]

                    shipName?.let {
                        unsafeJso<ShipSummaryResource> {
                            name = Text(shipName)
                        }
                    }
                },
                children = arrayOf(
                    RouteObject(
                        path = Site.Ships.CARGO,
                        Component = CargoPage
                    ),
                ),
            )
        )
    )
