package games.studiohummingbird.voyd.components

import games.studiohummingbird.voyd.resources.Site
import react.FC
import react.Props
import react.dom.html.ReactHTML.area
import react.dom.html.ReactHTML.map

val LocalMap = FC<Props> { props ->
    map {
        name = "local-map"
        area {
            shape = "circle"
            coords = "128,128,128"
            href = Site.SITE_MAP
        }
    }
}
