package games.studiohummingbird.voyd.pages

import games.studiohummingbird.voyd.PilotOverviewResource
import games.studiohummingbird.voyd.components.LocalMap
import games.studiohummingbird.voyd.practice.swtor.CrewMemberMissionSelectForm
import react.FC
import react.Props
import react.dom.html.ReactHTML.button
import react.dom.html.ReactHTML.details
import react.dom.html.ReactHTML.div
import react.dom.html.ReactHTML.h1
import react.dom.html.ReactHTML.h2
import react.dom.html.ReactHTML.hr
import react.dom.html.ReactHTML.section
import react.dom.html.ReactHTML.span
import react.dom.html.ReactHTML.summary
import react.router.dom.Link
import react.router.useLoaderData
import web.cssom.ClassName

val PilotOverview = FC<Props> { props ->
    val loaderData = useLoaderData().unsafeCast<PilotOverviewResource>()

    h1 {
        itemScope = true
        itemType = "https://schema.org/Thing"

        +"Pilot name: "
        span {
            itemProp = "name"
            +loaderData.name.string
        }
    }

    loaderData.embedded?.let { embedded ->
        hr { }
        div {
            className = ClassName("pilot-page-content")
            section {
                itemScope = true
                itemType = "need a new schema for the overview"

                embedded.gameLocation?.let { gameLocation ->
                    details {
                        itemProp = "gameLocation"
                        itemScope = true
                        itemType = "https://schema.org/Place"

                        summary {
                            h2 { +"Local System" }
                        }
                        Link {
                            itemProp = "name"
                            to = gameLocation.links.self.href
                            +gameLocation.name.string
                        }
                    }
                }

                embedded.contracts?.let { contracts ->
                    details {
                        itemScope = true
                        itemType = "https://schema.org/Thing"

                        summary {
                            h2 { +"Contracts" }
                        }
                        Link {
                            itemProp = "name"
                            to = contracts.links.self.href
                            +contracts.name.string
                        }
                    }
                }

                embedded.calendar?.let { calendar ->
                    details {
                        itemScope = true
                        itemType = "https://schema.org/Thing"

                        summary {
                            h2 { +"Calendar" }
                        }
                    }
                }
            }
            section {
                embedded.ship?.let { ship ->
                    details {
                        itemScope = true
                        itemType = "https://schema.org/Thing"

                        summary {
                            h2 { +"Ship" }
                        }
                        div {
                            className = ClassName("flex-column")

                            Link {
                                itemProp = "name"
                                to = ship.links.self.href
                                +ship.name.string
                            }

                            ship.links.cargo?.let { cargo ->
                                Link {
                                    to = cargo.href
                                    +"Cargo"
                                }
                            }
                        }
                    }
                }
            }
        }
        div {
            button {
                +"Jump"
            }
        }
        div {
            LocalMap { }
        }
        CrewMemberMissionSelectForm { }
    }
}
