package games.studiohummingbird.voyd.pages

import games.studiohummingbird.voyd.ShipSummaryResource
import react.FC
import react.Props
import react.dom.html.ReactHTML.div
import react.dom.html.ReactHTML.h1
import react.dom.html.ReactHTML.hr
import react.dom.html.ReactHTML.span
import react.router.Outlet
import react.router.useLoaderData

val ShipSummaryPage = FC<Props> { props ->
    val loaderData = useLoaderData().unsafeCast<ShipSummaryResource>()

    div {
        itemScope = true
        itemType = "https://schema.org/Thing"
        h1 {
            +"Ship:"
            span {
                itemProp = "name"
                +loaderData.name.string
            }
        }
        hr { }
        Outlet { }
    }
}
