package games.studiohummingbird.voyd.pages

import games.studiohummingbird.voyd.resources.Site
import react.FC
import react.Props
import react.dom.html.ReactHTML.details
import react.dom.html.ReactHTML.h2
import react.dom.html.ReactHTML.li
import react.dom.html.ReactHTML.nav
import react.dom.html.ReactHTML.summary
import react.dom.html.ReactHTML.ul
import react.router.dom.Link

val Sitemap = FC<Props> { props ->

    details {
        summary {
            h2 { +"Pilot" }
        }
        nav {
            ul {
                li {
                    Link {
                        to = Site.Pilot.INDEX
                        +"Pilot overview"
                    }
                }
                li {
                    Link {
                        to = Site.Pilot.LICENSES
                        +"Pilot licenses"
                    }
                }
            }
        }
    }

    details {
        summary {
            h2 { +"Ship" }
        }
        nav {
            ul {
                li {
                    Link {
                        to = Site.Ships.INDEX
                        +"Ship overview"
                    }
                }
            }
        }
    }
}
