package games.studiohummingbird.voyd.components

import games.studiohummingbird.chains.DomainEvent
import games.studiohummingbird.voyd.contexts.ClockContext
import games.studiohummingbird.voyd.resources.Site
import js.array.component1
import js.array.component2
import react.*
import react.dom.events.FormEventHandler
import react.dom.events.MouseEventHandler
import react.dom.html.ReactHTML.fieldset
import react.dom.html.ReactHTML.input
import react.dom.html.ReactHTML.label
import react.dom.html.ReactHTML.legend
import react.router.dom.Form
import remix.run.router.HTMLFormMethod
import web.cssom.ClassName
import web.html.*
import kotlin.time.ExperimentalTime
import kotlin.time.Instant

sealed interface CredentialSelectorEvent : DomainEvent

@OptIn(ExperimentalTime::class)
data class PilotNameChanged(
    override val occurredAt: Instant,
    val pilotName: String,
) : CredentialSelectorEvent

@OptIn(ExperimentalTime::class)
data class PasswordChanged(
    override val occurredAt: Instant,
    val password: String,
) : CredentialSelectorEvent

@OptIn(ExperimentalTime::class)
data class Submit(
    override val occurredAt: Instant,
) : CredentialSelectorEvent

sealed interface CredentialSelectorState {
    val pilotName: String
    val password: String

    operator fun plus(event: CredentialSelectorEvent): CredentialSelectorState
}

object InitialCredentialSelectorState : CredentialSelectorState {
    override val pilotName: String = ""
    override val password: String = ""

    override fun plus(event: CredentialSelectorEvent): CredentialSelectorState =
        ActiveCredentialSelectorState(pilotName, password) + event
}

data class ActiveCredentialSelectorState(
    override val pilotName: String,
    override val password: String,
) : CredentialSelectorState {
    override fun plus(event: CredentialSelectorEvent): CredentialSelectorState =
        when (event) {
            is PilotNameChanged -> copy(pilotName = event.pilotName)
            is PasswordChanged -> copy(password = event.password)
            is Submit -> SubmittedCredentialsState(pilotName, password)
        }
}

data class SubmittedCredentialsState(
    override val pilotName: String,
    override val password: String,
) : CredentialSelectorState {
    override fun plus(event: CredentialSelectorEvent): CredentialSelectorState =
        this
}

@OptIn(ExperimentalTime::class)
val CredentialSelector = FC<Props> { props ->
    val clock = use(ClockContext)
    val events = use(EventsContext)
    val appendEvent = use(AppendEventContext)
    val (credentialSelectorState, appendStateEvent) = useReducer(
        CredentialSelectorState::plus,
        InitialCredentialSelectorState
    )

    val onInputPilotName: FormEventHandler<HTMLInputElement> =
        useCallback(clock, appendEvent) { event ->
            val pilotName = event.currentTarget.value
            console.log("onInputPilotName", pilotName)
            appendEvent(
                PilotNameChanged(
                    clock.now(),
                    pilotName
                )
            )
        }

    val onInputPassword: FormEventHandler<HTMLInputElement> =
        useCallback(clock, appendEvent) { event ->
            val password = event.currentTarget.value
            console.log("onInputPassword", password)
            appendEvent(
                PasswordChanged(
                    clock.now(),
                    password
                )
            )
        }

    val onClickSubmit: MouseEventHandler<HTMLInputElement> =
        useCallback(clock, appendEvent) { event ->
            appendEvent(Submit(clock.now()))
        }

    useEffect(events, appendStateEvent) {
        val lastEvent = events.lastOrNull()// maybe replace with events since last check
        if (lastEvent is CredentialSelectorEvent) {
            appendStateEvent(lastEvent)
        }
    }

    useEffect(credentialSelectorState) {
        if (credentialSelectorState is SubmittedCredentialsState) {
            console.log("submitted credentials state")
            console.log("saving to sessionStorage")
        }
    }

    Form {
        action = Site.Pilot.AUTHENTICATE
        method = HTMLFormMethod.POST

        fieldset {
            className = ClassName("credential-selector flex-column")
            legend {
                +"Enter credentials"
            }
            label {
                className = ClassName("flex-row")
                +"pilot name"
                input {
                    disabled = credentialSelectorState is SubmittedCredentialsState
                    maxLength = 8
                    name = "pilotName"
                    onInput = onInputPilotName
                    required = true
                    type = InputType.text
                    value = credentialSelectorState.pilotName
                }
            }
            label {
                className = ClassName("flex-row")
                +"password"
                input {
                    disabled = credentialSelectorState is SubmittedCredentialsState
                    maxLength = 8
                    name = "password"
                    onInput = onInputPassword
                    required = true
                    type = InputType.password
                    value = credentialSelectorState.password

                }
            }
            input {
                disabled = credentialSelectorState is SubmittedCredentialsState
                type = InputType.submit
                onClick = onClickSubmit
            }
        }
    }
}
