package games.studiohummingbird.voyd.components

import games.studiohummingbird.voyd.ResourceLinks
import games.studiohummingbird.voyd.ThingResource
import react.FC
import react.Props
import react.dom.html.ReactHTML.div
import react.dom.html.ReactHTML.h1
import react.dom.html.ReactHTML.span
import react.router.useLoaderData

val LocationDetails = FC<Props> { props ->
    val loaderData = useLoaderData().unsafeCast<ThingResource<ResourceLinks>>()

    h1 {
        itemScope = true
        itemType = "https://schema.org/Thing"

        +"Location: "
        span {
            itemProp = "name"
            +loaderData.name.string
        }
    }

    div {
        +"location details"
    }
}
